%line {
    content: '';
    position: absolute;
    background: #fff;
    width: 0;
    bottom: 0;
}

$font-family: 'Voltaire', sans-serif;
$color: #fff;
$text-transform: uppercase;
$user-select: none;

@mixin transition($duration) {
    transition: #{$duration ease}
};

@mixin animate($animation, $duration, $method) {
    animation: $animation $duration $method;
};

@mixin keyframes($name) {
    @keyframes #{$name} {
        @content
    }
};


// Button back

.btn-container {
    position: absolute;
    padding: 5px 0;
    top: 5%;
    left: 0;
    z-index: 99;
    
    .btn-back {
        $padding-right: 10px;
        
        font-family: $font-family;
        text-transform: $text-transform;
        padding-right: $padding-right;
        user-select: $user-select;
        text-decoration: none;
        font-size: 25px;
        letter-spacing: 1px;
        color: black;
        
        .btn-arrow-icon {
            padding-right: $padding-right;
            height: 20px;
        }

        &::before {
            @extend %line;
            @include transition(.3s);
            @include animate(ani-btn, 1s, forwards);
            border-top-right-radius: 8px;
            border-bottom-right-radius: 8px;
            left: 0;
            width: 27%;
            height: 100%;
            z-index: -1;
        }

        &:hover {

            &::before {
                width: 100%;
            }
        }

        @include keyframes(ani-btn) {
            0% {
                left: -50%;
            }
            100% {
                left: 0;
            }
        }
    }
}

// Main page

.main-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;

    .title-author {
        %style-text {
            font-family: $font-family;
            text-transform: $text-transform;
            user-select: $user-select;
            letter-spacing: 3px;
            padding: 10px 0;
        }

        display: flex;
        flex-direction: column;
        position: relative;
        text-align: center;
        width: 100%;
        margin-bottom: 20%;

        .text-name {
            @extend %style-text;
            position: relative;
            font-size: 40px;
            font-weight: 600;
            margin-bottom: 10px;

            &::before {
                @extend %line;
                @include animate(name-animation, 1.5s, forwards);
                height: 100%;
                right: 0;
                z-index: -1;
            }

            @include keyframes(name-animation) {
                0% {
                    width: 0;
                }
                
                40% {
                    width: 0;
                }
            
                100% {
                    width: 100%;
                }
            }
        }
        
        .text-prof {
            @extend %style-text;
            @include animate(prof-animation, 2s, forwards);
            color: $color;
            position: absolute;
            font-size: 25px;
            right: 50%;
            transform: translateX(50%);
            opacity: 0;
            top: 0;

            @include keyframes(prof-animation) {
                0% {
                    opacity: 0;
                    top: 0;
                }
                50% {
                    opacity: 0;
                    top: 0;
                }
                100% {
                    opacity: 1;
                    top: 85%;
                }
            }
        }
    }

    .nav-container {
        list-style: none;
        padding: 0;
        display: flex;
        justify-content: space-around;

        .nav-items {
            position: relative;
            margin: 0 5px;
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: space-around;
            align-items: center;

            .nav-link {
                @include transition(1s);
                @include animate(nav-animation, 1s, forwards);
                font-family: $font-family;
                text-transform: $text-transform;
                user-select: $user-select;
                color: $color;
                font-size: 10px;
                text-decoration: none;
                letter-spacing: 1px;
                padding: 5px 0;
                filter: blur(10px); 
                opacity: 0; 
                z-index: 1;

                &::before {
                    @extend %line;
                    @include transition(.4s);
                    height: 50%;
                    left: 0;
                    bottom: 50%;
                    z-index: -1;
                }
                
                &::after {
                    @extend %line;
                    @include transition(.5s);
                    height: 50%;
                    right: 0;
                    z-index: -1;
                }
                
                &:hover {
                    color: #000;
                    
                    &::before, 
                    &::after {
                        width: 100%;
                    }
                }

                @include keyframes(nav-animation) {
                    0% {
                        opacity: 0; 
                        filter: blur(10px); 
                        font-size: 10px;
                        padding: 5px 0;
                    }
                    100% {
                        opacity: 1; 
                        filter: blur(0);
                        font-size: 30px;
                        padding: 5px 25px;
                    }
                }
            } 
        }
    }
}




@media screen and (max-width: 650px) {
    .nav-container {
        flex-direction: column;
        // align-items: center;
    }
    .title-author {
        margin-bottom: 50%;
    }
}






/* Contact page */

.contact-list {
    display: flex;
    flex-direction: column;
}

.contact-block {
    position: relative;
    display: flex;
    margin-bottom: 10px;
    width: 100%;
    justify-content: center;
    align-items: center;
}

.contact-text {
    font-family: 'Voltaire', sans-serif;
    text-align: center;
    font-size: 20px;
    color: #fff;
    width: 200px;
}

/* Animation contact page */

.contact-block::before {
    content: '';
    filter: blur(14px);
    position: absolute;
    width: 100%;
    height: 100%;
    right: 0;
    bottom: 0;
    background: #000;
    animation: ani-contacts 2s forwards;
}

@keyframes ani-contacts {
    0% {
        width: 100%;
    }
    40% {
        width: 70%;
    }
    100% {
        width: 0;
    }
}

.contact-logo {
    width: 75px;
    animation: ani-logo-contacts 1s forwards;
}

@keyframes ani-logo-contacts {
    0% {
        width: 150px;
        opacity: 0;
        filter: blur(10px);
    }
    100% {
        opacity: 1;
        width: 75px;
    }
}




h1 {
    color: #fff;
    animation: ani 3s forwards;
}

@keyframes ani {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}